.shakeVertical {
  -webkit-animation: shakeVertical 5s cubic-bezier(0.55, 0.055, 0.675, 0.19) infinite both;
  animation: shakeVertical 5s cubic-bezier(0.55, 0.055, 0.675, 0.19) infinite both; }

.shakeVerticalReverse {
  -webkit-animation: shakeVerticalReverse 5s cubic-bezier(0.55, 0.055, 0.675, 0.19) infinite reverse both;
  animation: shakeVerticalReverse 5s cubic-bezier(0.55, 0.055, 0.675, 0.19) infinite reverse both; }

.ldsHourglass {
  display: inline-block;
  position: absolute;
  width: 80px;
  bottom: 50%;
  left: 50%;
  height: 80px;
  -webkit-transform: translate(-50%);
          transform: translate(-50%); }

.ldsHourglass:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 8px;
  box-sizing: border-box;
  border: 32px solid #fff;
  border-color: #06d810 transparent #06d810 transparent;
  -webkit-animation: ldsHourglass 1.2s infinite;
          animation: ldsHourglass 1.2s infinite; }

@-webkit-keyframes ldsHourglass {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
    -webkit-animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
            animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19); }
  50% {
    -webkit-transform: rotate(900deg);
            transform: rotate(900deg);
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
            animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  100% {
    -webkit-transform: rotate(1800deg);
            transform: rotate(1800deg); } }

@keyframes ldsHourglass {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
    -webkit-animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
            animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19); }
  50% {
    -webkit-transform: rotate(900deg);
            transform: rotate(900deg);
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
            animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  100% {
    -webkit-transform: rotate(1800deg);
            transform: rotate(1800deg); } }

@-webkit-keyframes ldsRing {
